// Abstracts
@use '../../abstracts' as *;

// Borders
$border-map: 1px solid #928d7a;

// Component
.map {
    width: 100%;
    height: 250px;
    border: $border-map;

    @include mq($from: tablet) {
        height: 350px;
    }

    @include mq($from: desktop) {
        height: 450px;
    }
}
